import { lazy, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Swal from 'sweetalert2';
import moment from 'moment';

import { clearSearchingClients } from '../../actions/clients';
import { clearSearchingProducts } from '../../actions/products';
import { onView, onViewAction } from '../../actions/ui';
import { clearSearchingInvoices, getLastNumberInvoice, startAddNumberInvoiceActive, 
    startBeforeInvoiceActive, startClearInvoiceActive, startClearSavedCorrectAction, 
    startDeleteInvoice, startEditInvoice, startFirmarInvoice, startInvoiceActive, 
    startSaveInvoice1 } from '../../actions/invoice';

import { addOneToNumberDocument } from '../../helpers/addOneToNumberDocument';
import { isNumeric } from '../../helpers/standarClientValues';

import addIcon from '../../assets/addIcon.svg';
import editIcon from '../../assets/editIcon.svg';
import undoIcon from '../../assets/undoIcon.svg';
import deleteIcon from '../../assets/deleteIcon.svg';
import saveIcon from '../../assets/saveIcon.svg';
import firmaIcon from '../../assets/firmaIcon.svg';
import './facturas.css';

import ClienteInvoiceModal from './ClienteInvoiceModal';
import NewArticleInvoiceModal from './NewArticleInvoiceModal';
import EditArticleListModal from './EditArticleListModal';
import SearchInvoiceModal from './SearchInvoiceModal';
const InvoiceInfoCard = lazy( () => import('./InvoiceInfoCard') );
const InvoiceSelectsCard = lazy( () => import('./InvoiceSelectsCard') );
const InvoiceClientCard = lazy( () => import('./InvoiceClientCard') );
const InvoiceCommentCard = lazy( () => import('./InvoiceCommentCard') );
const InvoiceArticlesListCard = lazy( () => import('./InvoiceArticlesListCard') );
const InvoiceValuesCard = lazy( () => import('./InvoiceValuesCard') );

let autorizaActive = '';

const FacturasScreen = () => {

    const dispatch = useDispatch();

    const { user } = useSelector( state => state.auth );
    const { actionView } = useSelector( state => state.ui );
    const { activeInvoice, importaExistencia, 
        porcentajeIva, savedCorrect } = useSelector( state => state.invoice );

    const [width, setWidth] = useState(window.innerWidth); //Saber que ValuesCard se muestra
    const[ positionValuesCard, setPositionValuesCard ] = useState(true); 

    const [ blockCharge, setBlockCharge ] = useState(true);
    const [ blockNoLastInvoice, setBlockNoLastInvoice] = useState(true);
    const [ actionButton, setActionButton ] = useState(''); //actionButton: new, edit (Lo uso al guardar la factura)
    const [ beforeInvoice, setBeforeInvoice ] = useState([]);

    //Ancho de la pantalla
    useEffect(() => {
        const handleResize = () => {
            setWidth(window.innerWidth);
        }
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, [width]);
    
    //Cambiar de posición el InvoiceValuesCard según el ancho de la pantalla
    useEffect(() => {
        if(width <= 880)
            setPositionValuesCard(false);
        else if(width > 880)
            setPositionValuesCard(true);
    }, [ width, dispatch ]);

    //Saber en qué vista estamos y limpiar los datos de busqueda de clientes, productos y facturas
    useEffect(() => {
        localStorage.setItem( 'thisPage', 'facturas' );
        dispatch( onView( localStorage.getItem('thisPage') ) );

        dispatch( clearSearchingClients() );
        dispatch( clearSearchingProducts() );
        dispatch( clearSearchingInvoices() );
    }, [dispatch]);

    //Traer los datos necesarios para la factura en pantalla
    useEffect(() => {
        //Comenzar a traer la factura activa
        ( !activeInvoice && actionView !== 'newInvoice' && actionView !== 'editInvoice' )
            && dispatch( startInvoiceActive()); 
        
        //Traer el número de autorización de la última factura, si lo hay
        ( activeInvoice && activeInvoice.length > 0 && activeInvoice[0] && activeInvoice[0].autoriza
            && activeInvoice[0].autoriza.length > 0 )
            && ( autorizaActive = activeInvoice[0].autoriza.trim() );

        //Si hay más de una factura registrada
        ( activeInvoice && activeInvoice.length > 0 )
            && setBlockNoLastInvoice(false);

        //Si la factura se guardó correctamente, activar/desactivar campos.
        ( savedCorrect )
            && setBlockCharge(true);

    }, [actionView, activeInvoice, dispatch, savedCorrect, beforeInvoice]);

    const renderNewInvoiceNumber = (lastNumber) => {
        /* En caso de que haya activeInvoice, obtener el número de factura, caso contrario colocar 
        el 001001000000001 */
        let numberInvoiceActive = null;

        if( activeInvoice && activeInvoice.length > 0 ) {
            if( lastNumber && lastNumber.length > 0 ) {
                numberInvoiceActive = lastNumber;
            }
        }

        if( !numberInvoiceActive ){
            numberInvoiceActive = '001001000000001';
            return numberInvoiceActive; 
        }else{
            //Aumentar en 1 el número de un documento
            let newNumberInvoice = addOneToNumberDocument(numberInvoiceActive);
            return newNumberInvoice; 
        }
    }

    const handleNewInvoice = async() => {
        //Guardar activeInvoice antes de los cambios en beforeInvoice
        ( activeInvoice && beforeInvoice.length === 0 )
            && setBeforeInvoice( activeInvoice );

        //Guardar en el store la acción en factura para bloquear o no el 'buscar' en el navbar
        await dispatch( onViewAction( 'newInvoice' ) );

        //Limpiar facturas buscadas
        dispatch( clearSearchingInvoices() );

        //Limpiar botón de guardar
        dispatch( startClearSavedCorrectAction() );

        //Obtener el último número de factura
        const lastNumber = await dispatch( getLastNumberInvoice() );

        //Imprimir nuevo número de factura
        const newNumberInvoice = renderNewInvoiceNumber(lastNumber);
        
        //Limpiar activeInvoice
        dispatch( startClearInvoiceActive() );

        //Enviar al nuevo activeInvoice el número de factura
        await dispatch( startAddNumberInvoiceActive(newNumberInvoice) );
        
        //Desbloquear / bloquear inputs
        setBlockCharge(false);

        //Saber que acción estoy haciendo
        setActionButton('new');
    }

    const handleEditInvoice = () => {
        //Guardar activeInvoice antes de los cambios en beforeInvoice
        ( activeInvoice && beforeInvoice.length === 0 )
            && setBeforeInvoice( activeInvoice );

        //Limpiar facturas buscadas
        dispatch( clearSearchingInvoices() );

        //Realizar acción solo si hay una factura en pantalla
        if( activeInvoice[0].total && activeInvoice[0].total.trim().length > 0 ){
            //Limpiar botón de guardar
            dispatch( startClearSavedCorrectAction() );
    
            //Realizar acción solo si no está firmada una factura
            if( autorizaActive.length === 0 ){
                //Desbloquear / bloquear inputs
                setBlockCharge(false);
        
                //Saber que acción estoy haciendo
                setActionButton('edit');

                //Guardar en el store la acción en factura para bloquear o no el buscar en el navbar
                dispatch( onViewAction( 'editInvoice' ) );
            }else
                Swal.fire({
                    title: 'Alerta',
                    text: 'No se puede editar una factura firmada',
                    icon: 'warning',
                    confirmButtonText: 'Ok',
                    confirmButtonColor: "#0191CE",   
                });
        }else{
            Swal.fire({
                title: 'Alerta',
                text: 'No hay facturas para editar',
                icon: 'warning',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE",  
            });
        }
    }

    const handleReverse = () => {
        //Limpiar facturas buscadas
        dispatch( clearSearchingInvoices() );

        //Limpiar botón de guardar
        dispatch( startClearSavedCorrectAction() );

        Swal.fire({
            title: '¿Desea deshacer los cambios?',
            showDenyButton: true,
            confirmButtonText: 'Aceptar',
            confirmButtonColor: "#0191CE", 
            denyButtonText: `Cancelar`,
        }).then(( result ) => {
            if( result.isConfirmed ){
                //Regresar al activeInvoice anterior
                dispatch( startBeforeInvoiceActive(beforeInvoice) );

                //Bloquear / desbloquear inputs al cargar
                setBlockCharge(true);

                //Eliminar la actionView para desbloquear el buscar en el navbar
                dispatch( onViewAction( '' ) );

                //Limpiar beforeInvoice
                setBeforeInvoice([]);
            }
        })
    }

    const handleDeleteInvoice = () => {
        //Limpiar facturas buscadas
        dispatch( clearSearchingInvoices() );

        //Realizar acción solo si hay una factura en pantalla
        if( activeInvoice[0].total && activeInvoice[0].total.trim().length > 0 ){
            if( !activeInvoice ){
                Swal.fire({
                    title: 'Alerta',
                    text: 'No hay factura para eliminar',
                    icon: 'warning',
                    confirmButtonText: 'Ok',
                    confirmButtonColor: "#0191CE" 
                });
            }else{
                if( autorizaActive.length === 0 ){
                    //Limpiar botón de guardar
                    dispatch( startClearSavedCorrectAction() );
            
                    Swal.fire({
                        title: '¿Desea eliminar la factura?',
                        showDenyButton: true,
                        confirmButtonText: 'Aceptar',
                        confirmButtonColor: "#0191CE", 
                        denyButtonText: `Cancelar`,
                    }).then(( result ) => {
                        if( result.isConfirmed ){
                            //Eliminar la factura
                            dispatch( startDeleteInvoice(activeInvoice[0].factura) );
                        }
                    })
                }else{
                    Swal.fire({
                        title: 'Alerta',
                        text: 'No se puede eliminar una factura firmada',
                        icon: 'warning',
                        confirmButtonText: 'Ok',
                        confirmButtonColor: "#0191CE" 
                    });
                }
            }
        }else{
            Swal.fire({
                title: 'Alerta',
                text: 'No hay facturas para eliminar',
                icon: 'warning',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }
    }

    const handleSaveInvoice = () => {
        //Limpiar facturas buscadas
        dispatch( clearSearchingInvoices() );

        //Validar que los campos no estén vacíos
        if( activeInvoice[0].total && activeInvoice[0].total.trim().length > 0 
            && parseFloat(activeInvoice[0].total) > 0 ){

            if( !activeInvoice[0].cliente || !activeInvoice[0].listProducts 
                || activeInvoice[0].cliente.length === 0 || activeInvoice[0].listProducts.length === 0 ){
                Swal.fire({
                    title: 'Alerta',
                    text: 'No se puede guardar una factura sin cliente o sin productos',
                    icon: 'warning',
                    confirmButtonText: 'Ok',
                    confirmButtonColor: "#0191CE"
                });
            }else{
                if( !activeInvoice[0].fecha || !activeInvoice[0].vence ){
                    Swal.fire({
                        title: 'Alerta',
                        text: 'No se puede guardar una factura sin fechas',
                        icon: 'warning',
                        confirmButtonText: 'Ok',
                        confirmButtonColor: "#0191CE"
                    });
                }else{
                    //Validar que la fecha de vencimiento sea mayor a la fecha de emisión
                    if( moment(activeInvoice[0].fecha).format('LL') > moment(activeInvoice[0].vence).format('LL') ){
                        Swal.fire({
                            title: 'Alerta',
                            text: 'La fecha de vencimiento no puede ser menor a la fecha de emisión',
                            icon: 'warning',
                            confirmButtonText: 'Ok',
                            confirmButtonColor: "#0191CE"
                        });
                    }else{
                        if( actionButton === 'new' ){
                            //Guardar una factura solo con un número válido
                            if( activeInvoice[0].factura.length !== 15 || !isNumeric(activeInvoice[0].factura) ){
                                Swal.fire({
                                    title: 'Alerta',
                                    text: 'No se puede guardar una factura sin número o con un número incorrecto menor a 15 dígitos',
                                    icon: 'warning',
                                    confirmButtonText: 'Ok',
                                    confirmButtonColor: "#0191CE"
                                });
                            }else{
                                //Mensaje de si desea guardar la factura
                                Swal.fire({
                                    title: '¿Desea guardar la factura?',
                                    showDenyButton: true,
                                    confirmButtonText: 'Aceptar',
                                    confirmButtonColor: "#0191CE",
                                    denyButtonText: `Cancelar`,
                                }).then(( result ) => {
                                    if( result.isConfirmed ){
                                        //Guardar la factura
                                        dispatch( startSaveInvoice1(activeInvoice[0], user) );
                                    }
                                })
                            }
            
                        }else if ( actionButton === 'edit' ){
                            //Guardar edición de una factura solo si tiene un número válido
                            if( activeInvoice[0].factura.length !== 15 || !isNumeric(activeInvoice[0].factura) ){
                                Swal.fire({
                                    title: 'Alerta',
                                    text: 'No se puede editar una factura sin número o con un número incorrecto menor a 15 dígitos',
                                    icon: 'warning',
                                    confirmButtonText: 'Ok',
                                    confirmButtonColor: "#0191CE"
                                });
                            }else{
                                //Mensaje de si desea guardar la factura
                                Swal.fire({
                                    title: '¿Desea editar la factura?',
                                    showDenyButton: true,
                                    confirmButtonText: 'Aceptar',
                                    confirmButtonColor: "#0191CE",
                                    denyButtonText: `Cancelar`,
                                }).then(( result ) => {
                                    if( result.isConfirmed ){
                                        //Editar la factura
                                        dispatch( startEditInvoice(activeInvoice[0], user, activeInvoice[0].factura) );
                                    }
                                })
                            }
                        }
                    }
                }
            }
        }else{
            Swal.fire({
                title: 'Alerta',
                text: 'No se puede facturar en cero',
                icon: 'warning',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        
        }
    }

    const handleFirmarInvoice = () => {
        //Limpiar facturas buscadas
        dispatch( clearSearchingInvoices() );

        //Validar que los campos no estén vacíos
        if( activeInvoice[0].total && activeInvoice[0].total.trim().length > 0 ){
            if( !activeInvoice ){
                Swal.fire({
                    title: 'Alerta',
                    text: 'No hay factura para firmar',
                    icon: 'warning',
                    confirmButtonText: 'Ok',
                    confirmButtonColor: "#0191CE"
                });
            }else{
                if( autorizaActive.length === 0 ){
                    //Limpiar botón de guardar
                    dispatch( startClearSavedCorrectAction() );
            
                    Swal.fire({
                        title: '¿Desea firmar la factura?',
                        showDenyButton: true,
                        confirmButtonText: 'Aceptar',
                        confirmButtonColor: "#0191CE",
                        denyButtonText: `Cancelar`,
                    }).then(( result ) => {
                        if( result.isConfirmed ){
                            //Firmar la factura
                            dispatch( startFirmarInvoice(activeInvoice[0].factura) );
                        }
                    })
                }else{
                    Swal.fire({
                        title: 'Alerta',
                        text: 'La factura ya está firmada',
                        icon: 'warning',
                        confirmButtonText: 'Ok',
                        confirmButtonColor: "#0191CE"
                    });
                }
            }
        }else{
            Swal.fire({
                title: 'Alerta',
                text: 'No hay facturas para firmar',
                icon: 'warning',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }
    }

    return (
        <div className='invoice'>
            {/* Botones - Acciones */}
            {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                ?   <header className='actions-buttons-invoice'>
                        <div className='action-button'>
                            <button 
                                disabled={!blockCharge}
                                onClick={ handleNewInvoice }
                                className={!blockCharge ? 'btn-add-invoice disabled-button-action' : 'btn-add-invoice'}
                                // className='btn-add-invoice'
                            >
                                <img src={addIcon} alt='new' />
                            </button>
                            <span className='hidden-action-add'>Nuevo</span>
                        </div>

                        <div className='action-button'>
                            <button
                                // disabled={( !blockCharge === false ) ? blockNoLastInvoice}
                                disabled={ blockNoLastInvoice ? true : !blockCharge }
                                //hay un bloqueo porque no hay ultima factura? si : no, pero hay un bloqueo al cargar?
                                onClick={ handleEditInvoice }
                                className={!blockCharge ? 'btn-edit-invoice disabled-button-action' : 'btn-edit-invoice'}
                                // className='btn-edit-invoice'
                            >
                                <img className='icon-action-invoice' src={editIcon} alt='edit' />
                            </button>
                            <span className='hidden-action-edit'>Editar</span>
                        </div>

                        <div className='action-button'>
                            <button 
                                disabled={blockCharge}
                                className={blockCharge ? 'btn-reverse-invoice disabled-button-action' : 'btn-reverse-invoice'}
                                onClick={ handleReverse }
                                // className='btn-reverse-invoice'
                            >
                                <img src={undoIcon} alt='undo' />
                            </button>
                            <span className='hidden-action-reverse'>Atrás</span>
                        </div>

                        <div className='action-button'>
                            <button
                                disabled={ blockNoLastInvoice ? true : !blockCharge }
                                className={!blockCharge ? 'btn-delete-invoice disabled-button-action' : 'btn-delete-invoice'}
                                // className='btn-delete-invoice'
                                onClick={ handleDeleteInvoice }
                            >
                                <img className='icon-action-invoice' src={deleteIcon} alt='delete' />
                            </button>
                            <span className='hidden-action-delete'>Eliminar</span>
                        </div>

                        <div className='action-button'>
                            <button 
                                disabled={blockCharge}
                                // className='btn-save-invoice'
                                className={blockCharge ? 'btn-save-invoice disabled-button-action' : 'btn-save-invoice'}
                                onClick={ handleSaveInvoice }
                            >
                                <img src={saveIcon} alt='save' />
                            </button>
                            <span className='hidden-action-save'>Guardar</span>
                        </div>

                        <div className='action-button'>
                            <button
                                disabled={ blockNoLastInvoice ? true : !blockCharge }
                                className={!blockCharge ? 'btn-firmar-invoice disabled-button-action' : 'btn-firmar-invoice'}
                                // className='btn-firmar-invoice'
                                onClick={ handleFirmarInvoice }
                            >
                                <img src={firmaIcon} alt='firmar' />
                            </button>
                            <span className='hidden-action-firmar'>Firmar</span>
                        </div>

                    </header>
                :   <header className='actions-buttons-invoice'></header>
            }

            {/* Contenido de factura */}
            <section className='invoice-content'>
                
                {/* Encabezado de la factura */}
                <div className='invoice-header'>
                
                    <div className='title-invoice'>
                        <h1 className='page-title-invoice'>Registro de facturas</h1>
                        <h2 className='autorizacion-title'>Autorización:</h2>

                        {( activeInvoice && activeInvoice.length > 0 && activeInvoice[0] && activeInvoice[0].autoriza )
                            ? <p className='autorizacion-text'>{ activeInvoice[0].autoriza }</p>
                            : <p className='autorizacion-text'>-</p>
                        }
                    </div>

                    <div className='horizontal-cards-header-invoice'>
                        {!positionValuesCard &&
                            <div className='invoice-values-card-aux'>
                                { 
                                    ( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                                        ?   <InvoiceValuesCard activeInvoice={ activeInvoice } blockCharge={ blockCharge } />
                                        :   <div className='invoice-selects-card'></div>
                                }
                            </div>
                        }
                        {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                            ?   <InvoiceInfoCard activeInvoice={ activeInvoice } blockCharge={ blockCharge } />
                            :   <div className='invoice-selects-card'></div>
                        }

                        {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                            ?   <InvoiceSelectsCard activeInvoice={ activeInvoice } blockCharge={ blockCharge } />
                            :   <div className='invoice-selects-card'></div>
                        }

                        {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                            ?   <InvoiceClientCard activeInvoice={ activeInvoice } blockCharge={ blockCharge } />
                            :   <div className='invoice-selects-card'></div>
                        }

                        {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                            ?   <InvoiceCommentCard activeInvoice={ activeInvoice } blockCharge={ blockCharge } />
                            :   <div className='invoice-selects-card'></div>
                        }
                    </div>

                </div>
                
                {/* Detalle de la factura */}
                <div className='invoice-detail'>
                    {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                        &&   <InvoiceArticlesListCard activeInvoice={ activeInvoice } blockCharge={ blockCharge }  />
                    }
                    
                    {positionValuesCard &&
                        <div className='invoice-values-card'>
                            {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                                && <InvoiceValuesCard activeInvoice={ activeInvoice } blockCharge={ blockCharge }  />
                            }
                        </div>
                    }
                </div>
            </section>
            
            {/* Modales de la factura */}
            {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                &&  <ClienteInvoiceModal />
            }

            {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                &&  <NewArticleInvoiceModal />
            }

            {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                &&  <EditArticleListModal />
            }

            {( activeInvoice && importaExistencia.length > 0 && porcentajeIva.length > 0 )
                &&  <SearchInvoiceModal />
            }

        </div>
    )
}

export default FacturasScreen;