import Modal from 'react-modal/lib/components/Modal';
import { useDispatch, useSelector } from 'react-redux';

import { clearSearchingClients, startSearchingClients } from '../../actions/clients';
import { invoiceTaskFinish, startPutClientSerchingOnInvoice } from '../../actions/invoice';
import { uiCloseModal } from '../../actions/ui';
import { useForm } from '../../hooks/useForm';

import './facturas.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
    },
};
Modal.setAppElement('#root');

const ClienteInvoiceModal = () => {

    const dispatch = useDispatch();

    const { modalOpen } = useSelector(state => state.ui);
    const { clientsFind } = useSelector( state => state.client );
    
    const { values, handleInputChange, reset } = useForm({paramSearchValue:''});

    const { paramSearchValue } = values;

    const closeModal = (e) => {
        e.preventDefault();
        dispatch( uiCloseModal() );
        dispatch( clearSearchingClients() );
        reset();
    }

    const handleSearchClientsByParam = (e) => {
        e.preventDefault();

        ( paramSearchValue.trim().length > 0 ) 
            ? dispatch( startSearchingClients( paramSearchValue ) )
            : dispatch( clearSearchingClients() );
    }

    const handleSelectClient = (client) => {
        const clientToInvoice = {
            cliente: client.codigoc,
            ruc: client.ruc,
            nombrec: client.nombrec,
            direccion: client.direccion,
            telefono: client.telefono,
            email: client.e_mail,
            clienteRegistraIva: parseInt(client.reg_iva.trim()),
        }

        dispatch( startPutClientSerchingOnInvoice( clientToInvoice ) );

        //Indicador de tarea terminada
        dispatch( invoiceTaskFinish('listClient') );

        dispatch( uiCloseModal() );
        dispatch( clearSearchingClients() );
        reset();
    }

    return (
        <Modal
            isOpen={ modalOpen }
            onRequestClose={ closeModal }
            shouldCloseOnOverlayClick={false}
            style={ customStyles }
            closeTimeoutMS={ 200 }
            className="modal"
            overlayClassName="modal-fondo"
        >
            <div className='modal-search-client-container'>
                <form>
                    <div className='form-search-client-invoice'>
                        <label className='label-search-client-invoice'>
                            Criterio de búsqueda cliente:
                        </label>
                        <input 
                            type='text' 
                            className='input-search-client-invoice'
                            name='paramSearchValue'
                            value={ paramSearchValue }
                            onChange={ handleInputChange }
                            autoComplete='off'
                            placeholder='Buscar...' 
                        />

                        <div className='buttons-container-search-client-invoice'>
                            <button 
                                className='button-search-client-invoice'
                                onClick={ handleSearchClientsByParam }
                            >
                                Buscar
                            </button>
                            <button 
                                className='button-cancel-client-invoice' 
                                onClick={ closeModal }
                            >
                                Cancelar
                            </button>
                        </div>
                    </div>
                </form>

                <div className='container-list-find-clients-invoice'>
                    {( clientsFind ) ?
                        clientsFind.map( client => (
                            <div 
                                className='item-list-find-clients-invoice' 
                                key={ client.codigoc }
                                onClick={() => handleSelectClient( client ) }
                            >
                                <div className='item-info-client-invoice'>
                                    <p className='title-item-find-client'>Código: </p>
                                    <p className='detail-item-find-client'>{ client.codigoc }</p>
                                </div>
                                <div className='item-info-client-invoice'>
                                    <p className='title-item-find-client'>RUC: </p>
                                    <p className='detail-item-find-client'>{ client.ruc }</p>
                                </div>
                                <div className='item-info-client-invoice'>
                                    <p className='title-item-find-client'>Nombre: </p>
                                    <p className='detail-item-find-client'>{ client.nombrec }</p>
                                </div>
                                <hr className='hr-clients-invoice' />
                            </div>
                        ))
                        :   <div className='item-list-find-clients-invoice'>
                                <div className='item-info-client-invoice'>
                                    <p className='title-item-find-client'></p>
                                    <p className='detail-item-find-client'></p>
                                </div>
                                <hr className='hr-clients-invoice' />
                            </div>
                    }
                    
                </div>
            </div>
        </Modal>
    )
}

export default ClienteInvoiceModal;