import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from 'react-modal/lib/components/Modal';

import { invoiceFindSelectedAction, invoiceTaskFinish, startSearchingInvoices, 
        startShowInvoiceByNumber } from '../../actions/invoice';
import { uiCloseSearchInvoiceModal } from '../../actions/ui';

import './facturas.css';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
    },
};
Modal.setAppElement('#root');

const initValue = {paramSearchValue:''}

const SearchInvoiceModal = () => {

    const dispatch = useDispatch();

    const { modalSearchInvoice, paramSearchValueUi } = useSelector(state => state.ui);
    const { invoicesFind } = useSelector( state => state.invoice );

    const [searchValues, setSearchValues] = useState(initValue);

    const { paramSearchValue } = searchValues;

    //Si hay un parámetro de busqueda desde el Navbar, colocarlo en el input
    useEffect(() => {
        if( modalSearchInvoice && paramSearchValueUi && paramSearchValueUi !== paramSearchValue ){
            setSearchValues({
                ...searchValues,
                paramSearchValue: paramSearchValueUi
            });
            dispatch( startSearchingInvoices( paramSearchValueUi ) );
        }
    }, [dispatch, paramSearchValue, paramSearchValueUi, searchValues, 
        setSearchValues, modalSearchInvoice]);

    const handleInputChange = ({ target }) => {     
        setSearchValues({
            ...searchValues,
            [target.name]: target.value
        });
    }

    const closeModal = (e) => {
        e.preventDefault();
        dispatch( uiCloseSearchInvoiceModal() );
        setSearchValues( initValue );
    }

    const handleSearchInvoiceByParam = (e) => {
        e.preventDefault();

        dispatch( startSearchingInvoices( paramSearchValue ) );
    }

    const handleSelectInvoice = async(invoiceNumber) => {
        //Bandera en positivo
        await dispatch( invoiceFindSelectedAction() );
        dispatch( startShowInvoiceByNumber( invoiceNumber ) );

        //Indicador de tarea terminada
        dispatch( invoiceTaskFinish('listInvoices') );

        dispatch( uiCloseSearchInvoiceModal() );
        setSearchValues( initValue );
    }

    return (
        <Modal
            isOpen={ modalSearchInvoice }
            onRequestClose={ closeModal }
            shouldCloseOnOverlayClick={false}
            style={ customStyles }
            closeTimeoutMS={ 200 }
            className="modal"
            overlayClassName="modal-fondo"
        >
            <div className='modal-search-client-container'>
                <form>
                    <div className='form-search-client-invoice'>
                        <label className='label-search-client-invoice'>
                            Criterio de búsqueda facturas:
                        </label>
                        <input 
                            type='text' 
                            className='input-search-client-invoice'
                            name='paramSearchValue'
                            value={ paramSearchValue }
                            onChange={ handleInputChange }
                            autoComplete='off'
                            placeholder='Buscar...' 
                        />

                        <div className='buttons-container-search-client-invoice'>
                            <button 
                                className='button-search-client-invoice'
                                onClick={ handleSearchInvoiceByParam }
                            >
                                Buscar
                            </button>
                            <button 
                                className='button-cancel-client-invoice' 
                                onClick={ closeModal }
                            >
                                Cancelar
                            </button>
                        </div>
                    </div>
                </form>

                <div className='container-list-find-clients-invoice'>
                    {( invoicesFind ) 
                        ?   invoicesFind.map( invoice => (
                                <div 
                                    className='item-list-find-clients-invoice' 
                                    key={ invoice.factura }
                                    onClick={() => handleSelectInvoice( invoice.factura ) }
                                >
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>Factura: </p>
                                        <p className='detail-item-find-client'>
                                            { invoice.factura }
                                        </p>
                                    </div>
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>RUC: </p>
                                        <p className='detail-item-find-client'>
                                            { invoice.ruc }
                                        </p>
                                    </div>
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>Nombre: </p>
                                        <p className='detail-item-find-client'>
                                            { invoice.nombrec }
                                        </p>
                                    </div>
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>TOTAL: </p>
                                        <p className='detail-item-find-client green'>
                                            { '$'+invoice.total }
                                        </p>
                                    </div>
                                    <hr className='hr-clients-invoice' />
                                </div>
                            ))

                        :   <div className='item-list-find-clients-invoice'>
                                <div className='item-info-client-invoice'>
                                    <p className='title-item-find-client'></p>
                                    <p className='detail-item-find-client'></p>
                                </div>
                                <hr className='hr-clients-invoice' />
                            </div>
                    }

                </div>
            </div>
        </Modal>
    )
}

export default SearchInvoiceModal;