const removeCharacters = (s) => {
    s = s.toString();
    let checkedNew = s.replace(/\./g, "");
    checkedNew = checkedNew.replace(/,/g, "");
    return checkedNew;
}

export const isNumeric = (n) => {
    n = removeCharacters(n);
    return !isNaN(parseInt(n)) && isFinite(n);
}

export const grabarIva = (iva) => {
    if(iva)
        return 0;
    else
        return 1;
}

export const checkArt = (check) => {
    if(check)
        return 1;
    else
        return 0;
}