import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from 'react-modal/lib/components/Modal';
import Swal from 'sweetalert2';

import { useForm } from '../../hooks/useForm';
import { clearModalNewArticleWasOpened, modalNewArticleWasOpened, 
        uiActionModalNewArticle, uiCloseModalArticles, 
        uiOpenModalEditArticles } from '../../actions/ui';
import { clearSearchingProducts, startSearchingProductsByBodega, startSearchingProductsByBodegaConExistencias } from '../../actions/products';
import { startIdProductOnInvoiceActiveAux, startPutProductNoValuesOnInvoiceEmpty, 
        startPutProductNoValuesOnInvoiceFill } from '../../actions/invoice';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
    },
};
Modal.setAppElement('#root');

const NewArticleInvoiceModal = () => {
    const dispatch = useDispatch();

    const { modalOpenArticles } = useSelector(state => state.ui);
    const { productFind } = useSelector( state => state.product );
    const { storeAux } = useSelector( state => state.bodega );
    const { activeInvoice, importaExistencia } = useSelector( state => state.invoice );

    const { values, handleInputChange, reset } = useForm({paramSearchValue:''});

    const { paramSearchValue } = values;

    //Saber si el modal de nuevo artículo está abierto
    useEffect(() => {
        dispatch( modalNewArticleWasOpened() );
    }, [dispatch])

    const closeModal = (e) => {
        e.preventDefault();
        dispatch( uiCloseModalArticles() );
        dispatch( clearSearchingProducts() );
        dispatch( clearModalNewArticleWasOpened() );
        reset();
    }

    const handleSearchProductsByParam = (e) => {
        e.preventDefault();
        if( paramSearchValue.trim().length > 0 ){
            ( importaExistencia === "1" )
                ?   dispatch( startSearchingProductsByBodega( paramSearchValue, storeAux ) )
                :   dispatch( startSearchingProductsByBodegaConExistencias( paramSearchValue, storeAux ) );
        }else{
            dispatch( clearSearchingProducts() );
        } 
        // ( paramSearchValue.trim().length > 0 ) 
        //     ? dispatch( startSearchingProductsByBodega( paramSearchValue, storeAux ) )
        //     : dispatch( clearSearchingProducts() );
    }

    const handleSelectProduct = (product) => {
        /* Enviar a la lista inexistente el producto seleccionado sin valores para abrir el modal de 
        editar o eliminar. */

        //Preparamos el objeto para enviar al siguiente modal
        const productNoValuesToListInvoice = {
            bodega: product.bodega.trim(),
            codigo: product.codigoa.trim(),
            detalle: product.nombrea.trim(),
            precio: parseFloat(product.precio_1.trim()).toFixed(2)+'',
            impuesto: parseInt(product.impuesto.trim())+'',
            grupo: product.grupo.trim(), 
            servicio: product.servicio.trim(),
        }

        /* Enviar el código del artículo al store como auxiliar y ocuparlo en el modal de editar o 
        eliminar para saber que artículo se está editando o eliminando. */
        dispatch( startIdProductOnInvoiceActiveAux( product.codigoa.trim() ) );

        /* Identificar si hay una lista para añadir un producto o si no existe la lista para agregar 
        el primer producto. */
        if( activeInvoice[0].listProducts && activeInvoice[0].listProducts.length > 0 ) {
            //Validar que artículo no exista en la lista
            const productToList = activeInvoice[0].listProducts.filter(product1 =>
                (product1.codigo.trim() === product.codigoa.trim())
            );

            if( productToList.length === 0 ) {
                //Agregar artículo a una lista existente y con más artículos
                dispatch( startPutProductNoValuesOnInvoiceFill( productNoValuesToListInvoice ) );

                dispatch( uiCloseModalArticles() );
                dispatch( clearSearchingProducts() );
                /* Avisar al siguiente modal si apenas se va a ingresar un nuevo artículo o se requiere 
                editarlo o eliminarlo para reenderizar el botón de eliminar o no. */
                dispatch( uiActionModalNewArticle() );

                //Abrir el modal de editar o eliminar artículo en la lista.
                dispatch( uiOpenModalEditArticles() );

                reset();
            }else{
                Swal.fire({
                    title: 'Error',
                    text: 'El artículo ya se encuentra en la lista',
                    icon: 'error',
                    confirmButtonText: 'Ok',
                    confirmButtonColor: "#0191CE"
                });
            }
        }else{
            //Agregar el primer artículo a una lista vacía
            dispatch( startPutProductNoValuesOnInvoiceEmpty( productNoValuesToListInvoice ) );

            dispatch( uiCloseModalArticles() );
            dispatch( clearSearchingProducts() );
            /* Avisar al siguiente modal si apenas se va a ingresar un nuevo artículo o se requiere 
            editarlo o eliminarlo para reenderizar el botón de eliminar o no. */
            dispatch( uiActionModalNewArticle() );

            //Abrir el modal de editar o eliminar artículo en la lista.
            dispatch( uiOpenModalEditArticles() );

            reset();
        }
    }

    return (
        <Modal
            isOpen={ modalOpenArticles }
            onRequestClose={ closeModal }
            shouldCloseOnOverlayClick={false}
            style={ customStyles }
            closeTimeoutMS={ 200 }
            className="modal"
            overlayClassName="modal-fondo"
        >
            <div className='modal-search-client-container'>
                <form>
                    <div className='form-search-client-invoice'>
                        <label className='label-search-client-invoice'>
                            Criterio de búsqueda artículo:
                        </label>
                        <input 
                            type='text' 
                            className='input-search-client-invoice'
                            name='paramSearchValue'
                            value={ paramSearchValue }
                            onChange={ handleInputChange }
                            autoComplete='off'
                            placeholder='Buscar...' 
                        />

                        <div className='buttons-container-search-client-invoice'>
                            <button 
                                className='button-search-client-invoice'
                                onClick={ handleSearchProductsByParam }
                            >
                                Buscar
                            </button>
                            <button 
                                className='button-cancel-client-invoice' 
                                onClick={ closeModal }
                            >
                                Cancelar
                            </button>
                        </div>
                    </div>
                </form>

                <div className='container-list-find-clients-invoice'>
                    {( productFind ) 
                        ?   productFind.map( product => (
                                <div 
                                    className='item-list-find-clients-invoice' 
                                    key={ product.codigoa.trim() }
                                    onClick={() => handleSelectProduct( product ) }
                                >
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>Código: </p>
                                        <p className='detail-item-find-client'>
                                            { product.codigoa.trim() }
                                        </p>
                                    </div>
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>Precio: </p>
                                        <p className='detail-item-find-client'>
                                            { parseFloat(product.precio_1.trim()).toFixed(2)+'' }
                                        </p>
                                    </div>
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>Bodega: </p>
                                        <p className='detail-item-find-client'>
                                            { product.bodega.trim() }
                                        </p>
                                    </div>
                                    <div className='item-info-client-invoice'>
                                        <p className='title-item-find-client'>Nombre: </p>
                                        <p className='detail-item-find-client'>
                                            { product.nombrea.trim() }
                                        </p>
                                    </div>
                                    <hr className='hr-clients-invoice' />
                                </div>
                            ))
                            
                        :   <div className='item-list-find-clients-invoice'>
                                <div className='item-info-client-invoice'>
                                    <p className='title-item-find-client'></p>
                                    <p className='detail-item-find-client'></p>
                                </div>
                                <hr className='hr-clients-invoice' />
                            </div>
                    }
                </div>
            </div>
        </Modal>
    )
}

export default NewArticleInvoiceModal;