import { useEffect, useState } from 'react';
import Modal from 'react-modal/lib/components/Modal';
import { useDispatch, useSelector } from 'react-redux';
import Swal from 'sweetalert2';

import { invoiceTaskFinish, startClearIdProductOnInvoiceActiveAux, 
        startClearProductAddNoValuesOnInvoice, startDeleteProductFromInvoice, 
        startPutProductWithValuesOnInvoice } from '../../actions/invoice';
import { clearModalNewArticleWasOpened, 
        uiClearActionModalNewArticle, uiCloseModalEditArticles } from '../../actions/ui';
import { isNumeric } from '../../helpers/standarClientValues';

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
    },
};
Modal.setAppElement('#root');

let initValues = {
    nombreValueProductModal: '',
    cantidadValueProductModal: '',
    precio1ValueProductModal: '',
    pDesctoValueProductModal: '',
}

const EditArticleListModal = () => {
    const dispatch = useDispatch();

    const { 
        modalOpenEditArticles, 
        newArticleModal, 
        openNewArticleWasOpened } = useSelector(state => state.ui);
    const { activeInvoice, idProductAux } = useSelector( state => state.invoice );

    const [ productOnModal, setProductOnModal ] = useState(null);
    const [ productOnModalView, setProductOnModalView ] = useState(false);
    const [formValues, setFormValues] = useState( initValues );
    
    const { 
        nombreValueProductModal, 
        cantidadValueProductModal, 
        precio1ValueProductModal, 
        pDesctoValueProductModal } = formValues;

    //Listar el artículo seleccionado de la lista de artículos de la factura, en el modal
    if(activeInvoice[0] && activeInvoice[0].listProducts && activeInvoice[0].listProducts.length > 0 
        && idProductAux && idProductAux.trim().length > 0 && !productOnModalView ) {

        const productToList = activeInvoice[0].listProducts.filter(product => 
            (product.codigo.trim() === idProductAux.trim())
        );

        setProductOnModal(productToList[0]);
        setProductOnModalView(true);
    }

    //Cambiar a falso el productOnModalView cada vez que se abra el modal
    useEffect(() => {
        ( modalOpenEditArticles ) 
            && setProductOnModalView(false);
    }, [modalOpenEditArticles]);

    /* Colocar valores que trae el producto seleccionado en el modal, si es que los tiene caso 
    contrario los valores por defecto. */
    useEffect(() => {
        if( productOnModal ){
            let cantidadAux = '';
            let desctoAux = '';
            
            if( productOnModal.cantidad && productOnModal.cantidad.trim().length > 0 ){
                    cantidadAux = productOnModal.cantidad;
            }

            if( productOnModal.descto && productOnModal.cantidad.trim().length > 0 ){
                desctoAux = productOnModal.descto;
            }
                
            const productValues = {
                nombreValueProductModal: productOnModal.detalle,
                precio1ValueProductModal: parseFloat(productOnModal.precio.trim()).toFixed(2),
                cantidadValueProductModal: cantidadAux,
                pDesctoValueProductModal: desctoAux,
            }

            setFormValues( productValues );
        }else{
            setFormValues( initValues );
        }
    }, [productOnModal, idProductAux]);
    
    const handleInputChange = ({ target }) => {
        setFormValues({
            ...formValues,
            [target.name]: target.value
        });
    }

    const closeModal = (e) => {
        e.preventDefault();
        dispatch( uiCloseModalEditArticles() );
        dispatch(startClearIdProductOnInvoiceActiveAux());
        dispatch(uiClearActionModalNewArticle());

        /* Limpiar la lista de productos en factura quitando el producto seleccionado pues no escoge 
        valores, pero solo si no ha estado en la lista antes de abrir el modal. */
        ( openNewArticleWasOpened )
            && dispatch( startClearProductAddNoValuesOnInvoice( idProductAux ) );

        setFormValues( initValues );

        //Limpiar indicador de si vengo del modal new article
        dispatch( clearModalNewArticleWasOpened() );
    }

    const handleAddProduct = (e) => {
        e.preventDefault();
        
        if( parseFloat(precio1ValueProductModal.trim()) > 0 
            && parseFloat(cantidadValueProductModal.trim()) > 0 ){

            //Calcular el total del producto
            Swal.fire({
                title: '¿Desea guardar los cambios?',
                showDenyButton: true,
                confirmButtonText: 'Aceptar',
                confirmButtonColor: '#0191CE',
                denyButtonText: `Cancelar`,
            }).then(( result ) => {
                if( result.isConfirmed ){
                    let pDesctoValueProductAux = 0.00;
                    if( typeof pDesctoValueProductModal === 'string' 
                        && pDesctoValueProductModal.trim().length > 0 ){

                        pDesctoValueProductAux = parseFloat(pDesctoValueProductModal.trim());

                    }else if ( typeof pDesctoValueProductModal === 'number' )
                        pDesctoValueProductAux = pDesctoValueProductModal;
        
                    let totalArt = 0.0;
                    let porcentajeDescuento = 0.0;
        
                    if(isNumeric(cantidadValueProductModal.trim()) 
                        && isNumeric(precio1ValueProductModal.trim()) 
                        && isNumeric(pDesctoValueProductAux)) {

                        totalArt = parseFloat(cantidadValueProductModal.trim()) * parseFloat(precio1ValueProductModal.trim());
        
                        porcentajeDescuento = (parseFloat(pDesctoValueProductAux) * totalArt / 100);
                        totalArt = totalArt - porcentajeDescuento;
                    }
        
                    //Agregar el producto al listado de productos de la factura
                    const productToAdd = {
                        codigo: idProductAux.trim(),
                        cantidad: cantidadValueProductModal,
                        precio: precio1ValueProductModal,
                        descto: pDesctoValueProductAux,
                        total: totalArt.toFixed(2)+'',
                    }
                    dispatch( startPutProductWithValuesOnInvoice(productToAdd) );
        
                    dispatch(uiClearActionModalNewArticle());
                    dispatch( uiCloseModalEditArticles() );
                    dispatch(startClearIdProductOnInvoiceActiveAux());
                    setFormValues( initValues );
        
                    //Limpiar indicador de si vengo del modal new article
                    dispatch( clearModalNewArticleWasOpened() );
        
                    //Indicador de tarea terminada para actualizar valores
                    dispatch( invoiceTaskFinish('listArticles') );
                }
            })
        }else{
            Swal.fire({
                title: "Error",   
                text: 'Debe ingresar valores en cantidad y precio',   
                icon: "error",   
                showCancelButton: false,      
                confirmButtonColor: "#0191CE",   
                confirmButtonText: "OK",   
                closeOnConfirm: false,   
                closeOnCancel: false,
                customClass: "Custom_Cancel"
            });
        }
    }

    const handleDeleteProductFromList = (e) => {
        e.preventDefault();
        Swal.fire({
            title: '¿Desea eliminar este artículo?',
            showDenyButton: true,
            confirmButtonText: 'Aceptar',
            confirmButtonColor: '#0191CE',
            denyButtonText: `Cancelar`,
        }).then(( result ) => {
            if( result.isConfirmed ){
                //Eliminar el producto del listado de productos de la factura
                dispatch( startDeleteProductFromInvoice(idProductAux) );
                dispatch( uiCloseModalEditArticles() );
                dispatch(startClearIdProductOnInvoiceActiveAux());
                setFormValues( initValues );
        
                //Indicador de tarea terminada para actualizar valores NO VALE?????
                dispatch( invoiceTaskFinish('listArticles') );
                
                //Limpiar indicador de si vengo del modal new article
                dispatch( clearModalNewArticleWasOpened() );
            }
        })
    }

    return (
        <Modal
            isOpen={ modalOpenEditArticles }
            onRequestClose={ closeModal }
            shouldCloseOnOverlayClick={false}
            style={ customStyles }
            closeTimeoutMS={ 200 }
            className="modal"
            overlayClassName="modal-fondo"
        >
            <button className="btn-close-modal-x" onClick={ closeModal }>
                <i className="fa fa-2x fa-times"></i>
            </button>
            <form className='form-action-product-modal'>
                <header>
                    <h1 className='title-product-modal'>Artículo</h1>
                    {
                        <p className='required-fields-product-modal'>* Datos obligatorios</p>
                    }
                </header>
                <hr/>

                <div className='row-product-input-modal'>
                    <label>Bodega: </label>
                    <p className='code-client-modal'>
                        {( productOnModal ) 
                            ? productOnModal.bodega 
                            : '' 
                        }
                    </p>
                </div>

                <div className='row-product-input-modal'>
                    <label>Código: </label>
                    <p className='code-client-modal'>
                        {( productOnModal ) 
                            ? productOnModal.codigo 
                            : '' 
                        }
                    </p>
                </div>

                <div className='colum-product-input-modal'>
                    <label>
                        Nombre <i className='required-input'>*</i> 
                    </label>
                    <input 
                        type='text' 
                        autoComplete='off'
                        placeholder='ARTÍCULO 1'
                        name='nombreValueProductModal'
                        value={ nombreValueProductModal }
                        onChange={ handleInputChange } 
                    />
                </div>

                <div className='colum-product-input-modal'>
                    <label>
                        Cantidad <i className='required-input'>*</i>
                    </label>
                    <input 
                        type='number'
                        autoComplete='off'
                        placeholder='0.00'
                        name='cantidadValueProductModal'
                        value={ cantidadValueProductModal }
                        onChange={ handleInputChange } 
                    />
                </div>

                <div className='colum-product-input-modal'>
                    <label>
                        Precio <i className='required-input'>*</i>
                    </label>
                    <input 
                        type='number'
                        autoComplete='off'
                        placeholder='0.00'
                        name='precio1ValueProductModal'
                        value={ precio1ValueProductModal }
                        onChange={ handleInputChange } 
                    />
                </div>

                <div className='colum-product-input-modal'>
                    <label>
                        %Descto <i className='required-input'>*</i>
                    </label>
                    <input 
                        type='number'
                        autoComplete='off'
                        placeholder='0.00'
                        name='pDesctoValueProductModal'
                        value={ pDesctoValueProductModal }
                        onChange={ handleInputChange } 
                    />
                </div>

                <div className='buttons-actions-products-column'>
                    <button 
                        onClick={ handleAddProduct }
                        className='button-save-product-modal'>
                        {!newArticleModal
                            ? 'EDITAR ARTÍCULO'
                            : 'AGREGAR ARTÍCULO'
                        }
                    </button>

                    {!newArticleModal
                        && <button 
                                onClick={ handleDeleteProductFromList }
                                className='button-close-product-modal'>
                                QUITAR ARTÍCULO
                            </button>
                    }
                </div>
            </form>
        </Modal>
    )
}

export default EditArticleListModal;