export const addOneToNumberDocument = (numberDocument) => {
    let e = 0;
    let nd = numberDocument.trim().length;
    for (let i = nd; i >= 1; i--){
        let a = numberDocument.trim().substring(i-1, i);
        let aInt = parseInt(a);
        if(aInt >= 0 && aInt <= 9){
        }else{
            e = i + 1;
            i = 0;
        }
    }

    let ax;
    if( nd === 0 ){
        ax = "000000000001";
    }else if( e > 0 ){
        let a1 = numberDocument.trim().substring(e);
        let aLong = parseInt(a1);
        ax = "" + (aLong + 1);
        aLong = parseInt(ax);
        if( aLong > 0 ){
            let cero = "000000000000000";
            ax = cero.substring(0, (a1.length() - ax.trim().length)) + ax.trim();
        }
        ax = numberDocument.trim().substring(0, e-2) + ax;
    }else{
        ax = numberDocument.trim();
        let aLong = parseInt(ax);
        ax = "" + (aLong + 1);
        aLong = parseInt(ax);
        if( aLong > 0 ){
            let cero = "000000000000000";
            try {
                ax = cero.substring(0, ( nd - ax.trim().length)) + ax.trim();
            }catch (e){
                console.log(e);
            }
        }
    }

    return ax;
}